.explore-btn {
    position: relative;
    background-color: black;
    color: white;
    padding: 1vh 1vw;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-family: 'Damion', cursive;
    font-size: 5vw;
}

.explore-btn:hover {
    background-color: white;
    color: black;
    cursor: pointer;
    text-decoration: none;
}