.figure-caption{ 
    color: black !important;
    font-family: 'Damion', cursive;
    opacity: 100% !important;
    font-size: x-large !important;
    text-align: center;
}

.proj-figure {
    position: relative;
}

.figure-img {
    margin-bottom: 0% !important;
}

.text{
    font-family: 'Damion', cursive !important;
    background-color: black;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
}

.center-container {
    z-index: 1;
    top: 50%;
    position: absolute;
}

.proj-figure:hover .figure-img {
    color: rgb(161, 140, 179);
    opacity: 0.3;
  }
  
.proj-figure:hover .center-btns-in-row {
    opacity: 1;
}

.center-btns-in-row {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.center-btns-in-row:hover {
    text-decoration: none;
    cursor: pointer;
}

